import React, { Component } from 'react';
import { Table } from 'antd';
import moment from 'moment';

export default class TableClasses extends Component {
  state = {};

  columns = [
    {
      title: 'Tên lớp',
      dataIndex: 'className',
      key: 'className',
    },
    {
      title: 'Tên môn',
      dataIndex: 'subjectLabel',
      key: 'subjectLabel',
    },
    {
      title: 'Số học trình',
      dataIndex: 'noOfCredit',
      key: 'noOfCredit',
    },
    {
      title: 'Lớp ghép',
      dataIndex: 'classSubjectConcat',
      key: 'classSubjectConcat',
    },
    {
      title: 'Loại giờ',
      dataIndex: 'studyPeriodTypeLabel',
      key: 'studyPeriodTypeLabel',
    },
    {
      title: 'S.tiết CTĐT',
      dataIndex: 'noOfPeriodProgram',
      key: 'noOfPeriodProgram',
    },
    {
      title: 'S.tiết/tuần',
      dataIndex: 'periodInSession',
      key: 'periodInSession',
    },
    {
      title: 'Ca',
      dataIndex: 'studySessionLabel',
      key: 'studySessionLabel',
    },
    {
      title: 'Giai đoạn',
      dataIndex: 'splitPeriod',
      key: 'splitPeriod',
    },
    {
      title: 'Từ tuần',
      dataIndex: 'fromWeek',
      key: 'fromWeek',
    },
    {
      title: 'Đến tuần',
      dataIndex: 'toWeek',
      key: 'toWeek',
    },
    {
      title: 'Từ ngày',
      dataIndex: 'fromDate',
      key: 'fromDate',
      render: (text, record) => <span>{record.fromDate ? moment(record.fromDate).format('DD-MM-YYYY') : ""}</span>,
    },
    {
      title: 'Đến ngày',
      dataIndex: 'toDate',
      key: 'toDate',
      render: (text, record) => <span>{record.toDate ? moment(record.toDate).format('DD-MM-YYYY') : ""}</span>,
    },
    {
      title: 'Giáo viên',
      key: 'teacherName',
      dataIndex: 'teacherName',
    },
    {
      title: 'Phòng',
      dataIndex: 'roomName',
      key: 'roomName',
    },
  ];

  render() {
    const { data, rowKey } = this.props;
    return (
      <Table
        columns={this.columns}
        dataSource={data}
        scroll={{ x: true }}
        rowKey={rowKey}
      />
    );
  }
}



